import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { UsuarioListComponent } from './containers/usuario-list/usuario-list.component';
import { UsuarioFormComponent } from './containers/usuario-form/usuario-form.component';

const routes: Route[] = [
  { path: '', component: UsuarioListComponent },
  { path: 'novo', component: UsuarioFormComponent },
  { path: ':id/editar', component: UsuarioFormComponent },
];

@NgModule({
  declarations: [UsuarioListComponent, UsuarioFormComponent],
  imports: [CommonModule, RouterModule.forChild(routes)],
})
export class UsuarioModule {}
